html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  @include text(13px, 24px);
  min-width: 320px;
  margin: 0 auto;
  color: $text-color;

  @media ($tablet) {
    @include text(17px, 30px);
  }
}

a {
  color: black;
  text-decoration: none;
  transition: 0.3s;
}
